<template>
  <div id="loginUser">
    <Form ref="formItem" :model="formItem" :label-width="80" class="user-form" :rules="formRule">
      <FormItem label="" prop="utype">
        <RadioGroup v-model="formItem.utype">
          <Radio label="createStudent"></Radio>
          <Radio label="createTeacher"></Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="" prop="name">
        <Input v-model="formItem.name" placeholder="" :maxlength="10"/>
      </FormItem>
      <FormItem label="" prop="school">
        <Input v-model="formItem.school" placeholder="" :maxlength="50"/>
      </FormItem>
      <FormItem label="" prop="course" v-if="formItem.utype == 'createTeacher'">
        <Input v-model="formItem.course" placeholder="" :maxlength="30"/>
      </FormItem>
      <FormItem label="" prop="nianji" v-if="formItem.utype == 'createStudent'">
        <Input v-model="formItem.nianji" placeholder="" :maxlength="30"/>
      </FormItem>
      <FormItem label="" prop="banji" v-if="formItem.utype == 'createStudent'">
        <Input v-model="formItem.banji" placeholder="" :maxlength="10"/>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="signUp()"></Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'

export default {
  data () {
    return {
      formItem: {
        utype: 'createStudent',
        name: '',
        school: '',
        course: '',
        nianji: '',
        banji: ''
      },
      formRule: {
        utype: [
          {required: true, message: '', trigger: 'blur'}
        ],
        name: [
          {required: true, message: '', trigger: 'blur'}
        ],
        school: [
          {required: true, message: '', trigger: 'blur'}
        ],
        course: [
          {required: true, message: '', trigger: 'blur'}
        ],
        nianji: [
          {required: true, message: '', trigger: 'blur'}
        ],
        banji: [
          {required: true, message: '', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    signUp: function () {
      var vueThis = this
      vueThis.$refs['formItem'].validate((valid) => {
        if (valid) {
          let f = vueThis.formItem;
          let funcName = f.utype;
          let params = [f.name + '', f.school + '', f.course + ''];
          if (f.utype === 'createStudent') {
            params = [f.name + '', f.school + '', f.nianji + '', f.banji + ''];
          }
          vueThis.$Spin.show();
          vueThis.$store.dispatch('callNew', {funcName: funcName, params: params, goods: {name: ''}}).then((ret) => {
            vueThis.$Spin.hide();
            console.log('', ret);
            vueThis.showSuccess(', 3');
            _.delay(function () {
              vueThis.$router.push('/')
              location.reload();
            }, 3000)
          }, (error) => {
            console.log('', error);
            vueThis.$Spin.hide();
            vueThis.showErr('' + error);
          });
        } else {
          vueThis.showErr('');
        }
      })
    }
  },
  computed: mapState([
    'user'
  ])
}
</script>

<style>
  #loginUser {
  }
</style>



