<template>
  <div id="dataSubmit">
    <Form ref="formItem" :model="formItem" :label-width="80" class="user-form" :rules="formRule">
      <FormItem label="" prop="course">
        <Input v-model="formItem.course" placeholder=""/>
      </FormItem>
      <FormItem label="" prop="title">
        <Input v-model="formItem.title" placeholder=""/>
      </FormItem>
      <FormItem label="" prop="endtime">
        <DatePicker v-model="formItem.endtime" type="date" placeholder="" format="yyyyMMdd"></DatePicker>
      </FormItem>
      <FormItem label="" prop="content">
        <Input v-model="formItem.content" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder=""/>
      </FormItem>
      <FormItem label="" prop="studentIds">
        <Select v-model="formItem.studentIds" filterable multiple>
          <Option v-for="item in students" :value="item.address" :key="item.address">
            {{ item.name }}-{{ item.school }}-{{ item.nianji }}-{{ item.banji }}
          </Option>
        </Select>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="submitNode()"></Button>
      </FormItem>
    </Form>
    <Spin size="large" fix v-if="spinShow"></Spin>
  </div>
</template>

<script>
import {mapState} from 'vuex'

export default {
  data () {
    return {
      spinShow: false,
      students: [],
      formItem: {
        course: '',
        title: '',
        endtime: '',
        studentIds: [],
        content: ''
      },
      formRule: {
        course: [
          {required: true, type: 'string', message: '', trigger: 'blur'}
        ],
        title: [
          {required: true, type: 'string', message: '', trigger: 'blur'}
        ],
        endtime: [
          {required: true, type: 'date', message: '', trigger: 'blur'}
        ],
        studentIds: [
          {required: true, type: 'array', min: 1, message: '', trigger: 'blur'}
        ],
        content: [
          {required: true, type: 'string', message: '', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    submitNode: function () {
      var vueThis = this
      vueThis.$refs['formItem'].validate((valid) => {
        if (valid) {
          var f = vueThis.formItem
          let funcName = 'createHomework';
          let params = [f.course + '', f.title + '', vueThis.formItem.endtime.getTime(), f.content + '', f.studentIds];
          vueThis.spinShow = true
          vueThis.$store.dispatch('callNew', {funcName: funcName, params: params}).then((ret) => {
            vueThis.spinShow = false
            vueThis.showSuccess('');
            vueThis.$router.push('/homeworkMine')
          }, (error) => {
            vueThis.spinShow = false
            vueThis.showErr('' + error);
          });
        } else {
          vueThis.showErr('');
        }
      })
    },
    getStudents: function () {
      var vueThis = this
      let funcName = 'getAllStudent';
      vueThis.spinShow = true
      vueThis.$store.dispatch('simulateCallNew', {funcName: funcName}).then((ret) => {
        vueThis.students = JSON.parse(ret);
        vueThis.spinShow = false
      }, (error) => {
        vueThis.spinShow = false
        vueThis.showErr('' + error);
      });
    }
  },
  created () {
    this.getStudents()
  },
  computed: mapState([
    'user'
  ])
}
</script>

<style>
  #dataSubmit {
  }
</style>



