import NebPay from 'nebpay.js'
import _ from 'lodash'
import nebulas from 'nebulas'

// 2  mainnet
const httpRequestUrl = 'https://mainnet.nebulas.io';
const payCallBackUrl = 'https://pay.nebulas.io/api/mainnet/pay';

// 2  testnet
// const httpRequestUrl = 'https://testnet.nebulas.io';
// const payCallBackUrl = 'https://pay.nebulas.io/api/pay';

const nebpay = new NebPay();
const neb = new nebulas.Neb();
neb.setRequest(new nebulas.HttpRequest(httpRequestUrl));
const api = neb.api;

const actions = {
  // simulateCall
  simulateCallNew ({state, commit}, p) {
    let params = p.params || [];
    let dappAddress = state.dappAddress;
    console.log('BEGIN', p.funcName)
    return new Promise(function (resolve, reject) {
      nebpay.simulateCall(dappAddress, '0', p.funcName, JSON.stringify(params), {
        goods: p.goods,
        callback: payCallBackUrl,
        listener: function (resp) {
          console.log('END', p.funcName, resp)
          if (resp.result) {
            resolve(resp.result)
          } else {
            reject(resp)
          }
        }
      })
    })
  },
  // call
  callNew ({state, commit, dispatch}, p) {
    let params = p.params || [];
    let dappAddress = state.dappAddress;
    console.log('BEGIN', p.funcName)
    return new Promise(function (resolve, reject) {
      nebpay.call(dappAddress, '0', p.funcName, JSON.stringify(params), {
        goods: p.goods,
        callback: payCallBackUrl,
        listener: function (resp) {
          console.log('END', p.funcName, resp)
          if (resp.result) {
            resolve(resp.result)
          } else if (resp.txhash) {
            dispatch('intervalQuery', resp.txhash).then(function (result) {
              resolve(result)
            }, function (error) {
              reject(error)
            })
          } else {
            reject(resp)
          }
        }
      })
    })
  },
  intervalQuery ({dispatch}, txhash) {
    console.log('txhash', txhash)
    return new Promise(function (resolve, reject) {
      api.getTransactionReceipt({hash: txhash}).then(function (receipt) {
        if (_.get(receipt, 'status', '') === 1) {
          // success
          resolve(receipt)
        } else if (_.get(receipt, 'status', '') === 0) {
          // fail
          reject(receipt)
        } else {
          _.delay(function () {
            dispatch('intervalQuery', txhash).then(function (result) {
              resolve(result)
            })
          }, 5000)
        }
      });
    })
  },
  // 
  initBaseAccount ({dispatch, commit}) {
    var p = new Promise(function (resolve, reject) {
      // 
      commit('setAccountsError', null)
      commit('setUser', null)
      commit('setAccounts', null)
      commit('setNebPay', nebpay)

      commit('setAccountsError', '...')
      if (_.get(navigator, 'userAgent', '').toLowerCase().indexOf('chrome') === -1) {
        commit('setAccountsError', 'Chrome')
        return reject(new Error('Chrome'))
      }

      commit('setAccountsError', '...')
      if (!navigator.onLine) {
        commit('setAccountsError', '')
        return reject(new Error(''))
      }

      commit('setAccountsError', '...')
      _.delay(function () {
        if (typeof webExtensionWallet === 'undefined') {
          commit('setAccountsError', '')
          return reject(new Error(''))
        }

        commit('setAccountsError', '...')
        dispatch('simulateCallNew', {funcName: 'getUserAddress'}).then(function (result) {
          if (result === 'null') {
            commit('setAccountsError', '')
            return reject(new Error(''))
          } else {
            let account = result.slice(1, -1);
            commit('setAccounts', [account])

            commit('setAccountsError', '...')
            dispatch('simulateCallNew', {funcName: 'getUserInfo'}).then(function (ret) {
              if (ret === 'null') {
                commit('setAccountsError', '/')
                return resolve()
              } else {
                commit('setAccountsError', null)
                let user = JSON.parse(ret);
                commit('setUser', user)
                return resolve()
              }
            }, function (error) {
              commit('setAccountsError', '/')
              return reject(error)
            });
          }
        }, function (error) {
          commit('setAccountsError', '')
          return reject(error)
        })
      }, 500)
    })
    return p
  }
}

export default actions



